import React from 'react';
import { DialogBlock, DialogWrapper, BtnClose } from '../StyledComponents/_Dialog';

export default ({ response: { response_text }, handleDialog }) => (
    <DialogWrapper>
        <DialogBlock>
            <p>GOOD JOB</p>
            <p>DATA SUCCESSFULLY WRITTEN</p>
            <p>{response_text}</p>
            <BtnClose onClick={handleDialog}>Close</BtnClose>
        </DialogBlock>
    </DialogWrapper>
)