import styled from 'styled-components';

const DialogWrapper = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100vh;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0,0,0,.6);
    z-index: 100;
`

const BtnClose = styled.button`
    padding: 6px 22px;
    position: absolute;
    right: 18px;
    bottom: 8px;
    font-size: 16px;
    border-radius: 5px;
    background: #868eb7;
    color: white;
`

const DialogBlock = styled.div`
    width: 30%;
    min-height: 15%;
    background-color: #3f51b5;
    padding: 30px;
    border-radius: 5px;
    color: white;
    font-size: 20px;
    animation: showResponse .4s;
    opacity: 0.9;
    position: relative;
    @keyframes showResponse{
        from {
            opacity: 0;
            transform: translateX(100%);
        } to {
            opacity: 0.9;
            transform: translateX(0);
        }
    }
`

export { DialogBlock, DialogWrapper, BtnClose }