import React, { Component } from 'react';

import { rows } from '../../data/state';
import uniqid from 'uniqid';
import { findByHour } from '../../helpers/helpers';
import Dialog from '../../components/Dialog/Dialog';
import Row from '../Row/Row';
import Body from '../../components/Main/Body';
import { Submit, WrapBtn } from '../../components/StyledComponents/_Body';

class Content extends Component{

    state = {
        rows,
        response: {
            success: false,
            response_text: null
        }
    }
    
    handleCheckRow = (hour, disabled) => {
        const rows = [...this.state.rows];
        const row = findByHour(rows, hour);
        let activeRow = row.disabled = !disabled;
        if(activeRow) row.tasks = [];
        this.setState({ rows });
    }

    addTaskToRow = (data) => {
        const rows = [...this.state.rows];
        const row = findByHour(rows, data.HOUR);
        row.tasks.push(data);
        this.setState({ rows })
    }

    removeTaskFromRow = (hour, ID) => {
        const rows = [...this.state.rows];
        const row = findByHour(rows, hour);
        row.tasks = row.tasks.filter(elem => elem.ID !== ID);
        this.setState({ rows });
    }

    showInfo = (hour, minute) => {
        const rows = [...this.state.rows];
        const row = findByHour(rows, hour);
        const task = row.tasks.find(item => item.MINUTE === minute);
        if(task.TYPE !== 'ICEBERG_ORDER'){
            delete task.PEAK_PRICE_DELTA;
            delete task.DISPLAY_QUANTITY;
        }
        return { ...task }
    }

    renderRows = (rows = []) => {
        return rows.map(({ hour, disabled, tasks }) => (
            <Row 
                checkActive={(hour, disabled) => this.handleCheckRow(hour, disabled)}
                key={hour} 
                active={disabled}
                tasks={tasks} 
                hour={hour}
                addTaskToRow={data => this.addTaskToRow(data)}
                delTask = {(hour, minute) => this.removeTaskFromRow(hour, minute)}
                showInfo = {(hour, minute) => this.showInfo(hour, minute)}
                />
        )); 
    }

    dataForApi = () => {
        const rows = [...this.state.rows];
        const list_hour = [];
        rows.forEach(row => {
            row.tasks.forEach(task => {
                if(task.TYPE !== 'ICEBERG_ORDER'){
                    delete task.PEAK_PRICE_DELTA;
                    delete task.DISPLAY_QUANTITY;
                    delete task.ID;
                }
                list_hour.push(task);
            })
        })
        return list_hour;
    }

    sendData = async () => {
        try{
            const data = this.dataForApi();
            const response = await fetch('/orders_management/', {
                method: 'POST',
                headers: {
                    'content-type': 'application/json',
                },
                body: JSON.stringify(data)
            });
            const response_code = await response.status;
            const response_text = await response.json();
            if (response_code === 400){
                this.setState({ response: { success: false } });
            }
            else if(response_code === 200){
                this.setState({ response: { success: true,  response_text } })
            }
        }
        catch(e){
            console.log(`Error ${e}`)
        }
    }

    componentDidMount = async () => {
        const data = await this.getDataApi();
        const rows = this.reFormat(data);
        this.setState({ rows });
    }

    reFormat = (data=[]) => {
        const _rows = [...rows];
        data.forEach(item => {
            item.ID = uniqid();
            _rows.forEach(row => {
                if(item.HOUR === row.id){
                    row.tasks.push(item)
                }
            })
        })
        return _rows;
    }

    getDataApi = async () => {
        try {
            const response = await fetch('/orders_management/')
            const data = await response.json();
            return data;
        } catch (err) {
            console.log(err);
        }
    }

    handleCloseModal = () => {
        this.setState({ response: { success: false } })
    }

    render(){
        console.log(this.state.response)
        return (
            <Body>
                {this.renderRows(this.state.rows)}
                <WrapBtn>
                    <Submit onClick={this.sendData}>Submit</Submit>
                </WrapBtn>
                {this.state.response.success && <Dialog handleDialog={this.handleCloseModal} response={this.state.response}/>}
            </Body>
        )
    }
}

export default Content;